/* global document, Office, Word */

import CVSS31 from "./cvss31";

const metricOrder = [
  "AV",
  "AC",
  "PR",
  "UI",
  "S",
  "C",
  "I",
  "A",
  "E",
  "RL",
  "RC",
  "CR",
  "IR",
  "AR",
  "MAV",
  "MAC",
  "MPR",
  "MUI",
  "MS",
  "MC",
  "MI",
  "MA",
];

const optionalMetrics = new Set(metricOrder.slice(8));

let toggleController;
let activateMetricsTab;

Office.onReady((info) => {
  if (info.host === Office.HostType.Word) {
    document.getElementById("sideload-msg").style.display = "none";
    document.getElementById("app-body").style.display = "flex";
  }
  initializeForm();
});

function initializeForm() {
  const form = document.getElementById("cvss-form");
  const resetButton = document.getElementById("reset-form");
  const loadButton = document.getElementById("load-selection");
  const updateButton = document.getElementById("update-selection");
  const insertButton = document.getElementById("insert-results");

  if (!form) {
    return;
  }

  form.addEventListener("submit", (event) => {
    event.preventDefault();
    handleCalculate(form);
  });

  toggleController = initializeOptionToggles(form, () => handleCalculate(form));
  activateMetricsTab = initializeMetricTabs(form);
  toggleController?.clearAll();
  initializeCopyButtons();
  condenseMetricButtons();

  if (loadButton) {
    loadButton.addEventListener("click", () => {
      loadFromSelection(form);
    });
  }

  if (updateButton) {
    updateButton.addEventListener("click", () => {
      updateSelectionFromForm(form);
    });
  }

  if (insertButton) {
    insertButton.addEventListener("click", () => {
      insertResultsIntoDocument(form);
    });
  }

  if (resetButton) {
    resetButton.addEventListener("click", () => {
      form.reset();
      clearResults();
      toggleController?.clearAll();
      activateMetricsTab?.("base");
      handleCalculate(form);
    });
  }

  clearResults();
  handleCalculate(form);
}

function handleCalculate(form) {
  const errorMessage = document.getElementById("error-message");
  const calculation = calculateMetricsFromForm(form);

  if (!calculation.success) {
    if (calculation.error?.errorType === "MissingBaseMetric") {
      renderError(errorMessage, null);
    } else {
      renderError(errorMessage, calculation.error);
    }
    clearScoreFields();
    return calculation;
  }

  renderError(errorMessage, null);

  updateResults(calculation.result, calculation.xmlString);
  return calculation;
}

function getMetricValues(form) {
  const values = {};

  metricOrder.forEach((metric) => {
    const field = form.elements.namedItem(metric);

    if (!field) {
      values[metric] = optionalMetrics.has(metric) ? "X" : "";
      return;
    }

    if (field.value && field.value !== "") {
      values[metric] = field.value;
    } else if (optionalMetrics.has(metric)) {
      values[metric] = "X";
    } else {
      values[metric] = "";
    }
  });

  return values;
}

function initializeMetricTabs(form) {
  const tabs = Array.from(form.querySelectorAll(".metric-tab"));
  const sections = Array.from(form.querySelectorAll(".metric-group"));

  if (tabs.length === 0 || sections.length === 0) {
    return;
  }

  const activate = (sectionId) => {
    tabs.forEach((tab) => {
      const isTarget = tab.dataset.section === sectionId;
      tab.classList.toggle("is-active", isTarget);
      tab.setAttribute("aria-selected", isTarget ? "true" : "false");
      tab.setAttribute("tabindex", isTarget ? "0" : "-1");
    });

    sections.forEach((section) => {
      const isTarget = section.dataset.section === sectionId;
      section.classList.toggle("is-active", isTarget);
      section.hidden = !isTarget;
      section.setAttribute("aria-hidden", isTarget ? "false" : "true");
    });
  };

  tabs.forEach((tab) => {
    tab.addEventListener("click", () => activate(tab.dataset.section));

    tab.addEventListener("keydown", (event) => {
      if (event.key !== "ArrowLeft" && event.key !== "ArrowRight") {
        return;
      }

      event.preventDefault();
      const currentIndex = tabs.indexOf(tab);
      const direction = event.key === "ArrowRight" ? 1 : -1;
      const nextIndex = (currentIndex + direction + tabs.length) % tabs.length;
      tabs[nextIndex].focus();
      activate(tabs[nextIndex].dataset.section);
    });
  });

  const initialTab =
    tabs.find((tab) => tab.classList.contains("is-active")) || tabs[0];
  activate(initialTab.dataset.section);
  return activate;
}

function initializeOptionToggles(form, onChange) {
  const groups = Array.from(form.querySelectorAll(".option-toggle-group"));
  const map = new Map();

  groups.forEach((group) => {
    const buttons = Array.from(group.querySelectorAll(".option-toggle"));
    if (buttons.length === 0) {
      return;
    }
    const name = buttons[0].dataset.name;
    if (!name) {
      return;
    }
    group.dataset.metric = name;
    buttons.forEach((button, index) => {
      button.setAttribute("role", "radio");
      button.setAttribute("aria-checked", "false");
      button.setAttribute("tabindex", index === 0 ? "0" : "-1");
    });
    map.set(name, { group, buttons });
  });

  const setValue = (name, value, notify = false) => {
    const entry = map.get(name);
    if (!entry) {
      return;
    }
    const hidden = form.elements.namedItem(name);
    if (!hidden) {
      return;
    }
    const isOptional = optionalMetrics.has(name);
    let target = value && value !== "" ? value : hidden.value;

    if (!target || target === "") {
      hidden.value = "";
      entry.buttons.forEach((button, index) => {
        button.classList.remove("is-selected");
        button.setAttribute("aria-checked", "false");
        button.setAttribute("tabindex", index === 0 ? "0" : "-1");
      });
      if (notify && typeof onChange === "function" && !isOptional) {
        onChange();
      }
      return;
    }

    hidden.value = target;

    entry.buttons.forEach((button) => {
      const isSelected = button.dataset.value === target;
      button.classList.toggle("is-selected", isSelected);
      button.setAttribute("aria-checked", isSelected ? "true" : "false");
      button.setAttribute("tabindex", isSelected ? "0" : "-1");
    });

    if (notify && typeof onChange === "function") {
      onChange();
    }
  };

  map.forEach((entry, name) => {
    entry.buttons.forEach((button) => {
      button.addEventListener("click", () => {
        setValue(name, button.dataset.value, true);
        button.focus();
      });

      button.addEventListener("keydown", (event) => {
        if (
          event.key !== "ArrowLeft" &&
          event.key !== "ArrowRight" &&
          event.key !== "ArrowUp" &&
          event.key !== "ArrowDown"
        ) {
          return;
        }

        event.preventDefault();

        const buttons = entry.buttons;
        const currentIndex = buttons.indexOf(button);
        const direction =
          event.key === "ArrowRight" || event.key === "ArrowDown" ? 1 : -1;
        const nextIndex = (currentIndex + direction + buttons.length) % buttons.length;
        const nextButton = buttons[nextIndex];
        setValue(name, nextButton.dataset.value, true);
        nextButton.focus();
      });
    });
  });

  const syncAll = () => {
    map.forEach((_, name) => {
      const hidden = form.elements.namedItem(name);
      setValue(name, hidden ? hidden.value : "", false);
    });
  };

  syncAll();

  if (map.size === 0) {
    return null;
  }

  return {
    setValue: (name, value) => setValue(name, value, false),
    syncAll,
    has: (name) => map.has(name),
    clearAll: () => {
      map.forEach((entry, name) => {
        const hidden = form.elements.namedItem(name);
        if (hidden) {
          hidden.value = "";
        }
        entry.buttons.forEach((button, index) => {
          button.classList.remove("is-selected");
          button.setAttribute("aria-checked", "false");
          button.setAttribute("tabindex", index === 0 ? "0" : "-1");
        });
      });
    },
  };
}

function initializeCopyButtons() {
  const buttons = Array.from(document.querySelectorAll(".copy-button"));
  if (buttons.length === 0) {
    return;
  }

  buttons.forEach((button) => {
    button.addEventListener("click", async () => {
      const targetId = button.dataset.copyTarget;
      const target = targetId ? document.getElementById(targetId) : null;
      if (!target) {
        return;
      }

      const value =
        target.tagName === "CODE"
          ? target.textContent.trim()
          : target.textContent.trim();
      if (!value || value === "-" || value === "") {
        return;
      }

      try {
        await navigator.clipboard.writeText(value);
        flashCopyStatus(button);
      } catch (error) {
        console.error("Failed to copy score to clipboard:", error);
      }
    });
  });
}

function condenseMetricButtons() {
  const buttons = Array.from(document.querySelectorAll(".option-toggle"));
  if (buttons.length === 0) {
    return;
  }

  buttons.forEach((button) => {
    if (button.dataset.compacted === "true") {
      return;
    }

    const titleSpan = button.querySelector(".option-title");
    if (!titleSpan) {
      return;
    }

    const fullTitle = titleSpan.textContent.trim();
    if (!fullTitle) {
      return;
    }

    const hintSpan = button.querySelector(".option-hint");
    const hintText = hintSpan ? hintSpan.textContent.trim() : "";
    const tooltipText = hintText ? `${fullTitle} - ${hintText}` : fullTitle;
    const abbreviated = fullTitle.charAt(0).toUpperCase();

    button.title = tooltipText;
    button.setAttribute("aria-label", tooltipText);
    titleSpan.textContent = abbreviated;
    button.classList.add("is-condensed");
    button.dataset.compacted = "true";

    if (hintSpan) {
      hintSpan.setAttribute("aria-hidden", "true");
      hintSpan.textContent = "";
    }
  });
}

function flashCopyStatus(button) {
  const originalLabel = button.textContent;
  button.textContent = "Copied!";
  button.classList.add("copy-success");

  setTimeout(() => {
    button.textContent = originalLabel;
    button.classList.remove("copy-success");
  }, 1500);
}

function renderError(container, result) {
  if (!container) {
    return;
  }

  if (!result) {
    container.textContent = "";
    return;
  }

  if (typeof result === "string") {
    container.textContent = result;
    return;
  }

  const metricList = Array.isArray(result.errorMetrics) ? ` (${result.errorMetrics.join(", ")})` : "";
  container.textContent = `${result.errorType}${metricList}`;
}

function updateResults(result, xmlString) {
  setText("base-score", result.baseMetricScore);
  setText("base-severity", formatSeverity(result.baseSeverity));
  setText("temporal-score", result.temporalMetricScore);
  setText("temporal-severity", formatSeverity(result.temporalSeverity));
  setText("environmental-score", result.environmentalMetricScore);
  setText("environmental-severity", formatSeverity(result.environmentalSeverity));
  setText("vector-string", result.vectorString);
  setTextArea("xml-output", xmlString);
}

function applyMetricsToForm(form, metrics) {
  metricOrder.forEach((metric) => {
    const field = form.elements.namedItem(metric);
    if (!field) {
      return;
    }

    const value = metrics.hasOwnProperty(metric) ? metrics[metric] : undefined;

    if (field.type === "hidden" && toggleController?.has(metric)) {
      const target =
        typeof value === "string" && value !== ""
          ? value
          : optionalMetrics.has(metric)
          ? "X"
          : field.defaultValue || field.value;
      toggleController.setValue(metric, target);
    } else if (field.type === "hidden") {
      field.value =
        typeof value === "string" && value !== ""
          ? value
          : optionalMetrics.has(metric)
          ? "X"
          : field.defaultValue || field.value;
    } else if (typeof value === "string" && value !== "") {
      field.value = value;
    } else if (optionalMetrics.has(metric)) {
      field.value = "X";
    }
  });
}

function clearResults() {
  clearScoreFields();
  setText("vector-string", "-");
  setTextArea("xml-output", "");
  const errorMessage = document.getElementById("error-message");
  if (errorMessage) {
    errorMessage.textContent = "";
  }
}

function clearScoreFields() {
  setText("base-score", "-");
  setText("base-severity", "");
  setText("temporal-score", "-");
  setText("temporal-severity", "");
  setText("environmental-score", "-");
  setText("environmental-severity", "");
}

function setText(id, value) {
  const element = document.getElementById(id);
  if (element) {
    element.textContent = value;
  }
}

function setTextArea(id, value) {
  const element = document.getElementById(id);
  if (element) {
    element.value = value;
  }
}

function formatSeverity(severity) {
  return severity ? `(${severity})` : "";
}

async function loadFromSelection(form) {
  const errorMessage = document.getElementById("error-message");

  if (Office.context.host !== Office.HostType.Word) {
    renderError(errorMessage, "Loading from the document is only available in Word.");
    return;
  }

  try {
    const selectionText = await Word.run(async (context) => {
      const selection = context.document.getSelection();
      selection.load("text");
      await context.sync();
      return selection.text || "";
    });

    const candidate = selectionText.trim();

    if (!candidate) {
      renderError(errorMessage, "Please select a CVSS v3.1 vector string in the document.");
      return;
    }

    const normalized = candidate.replace(/\s+/g, "");

    if (!CVSS31.vectorStringRegex_31.test(normalized)) {
      renderError(errorMessage, "Selection does not contain a valid CVSS v3.1 vector string.");
      return;
    }

    const metrics = extractMetricsFromVector(normalized);

    if (!metrics) {
      renderError(errorMessage, "Unable to parse the selected vector string.");
      return;
    }

    applyMetricsToForm(form, metrics);
    renderError(errorMessage, null);
    handleCalculate(form);
  } catch (error) {
    renderError(errorMessage, "Unable to read selection. Try again.");
    console.error("Failed to load CVSS vector from selection:", error);
  }
}

function extractMetricsFromVector(vectorString) {
  if (!CVSS31.vectorStringRegex_31.test(vectorString)) {
    return null;
  }

  const fragments = vectorString.substring(CVSS31.CVSSVersionIdentifier.length).split("/");
  const metrics = {};

  fragments.forEach((fragment) => {
    if (!fragment) {
      return;
    }

    const [metric, value] = fragment.split(":");
    if (metric && value) {
      metrics[metric] = value;
    }
  });

  return metrics;
}

function calculateMetricsFromForm(form) {
  const values = getMetricValues(form);

  const result = CVSS31.calculateCVSSFromMetrics(
    values.AV,
    values.AC,
    values.PR,
    values.UI,
    values.S,
    values.C,
    values.I,
    values.A,
    values.E,
    values.RL,
    values.RC,
    values.CR,
    values.IR,
    values.AR,
    values.MAV,
    values.MAC,
    values.MPR,
    values.MUI,
    values.MS,
    values.MC,
    values.MI,
    values.MA
  );

  if (!result.success) {
    return { success: false, error: result, values };
  }

  const xmlResult = CVSS31.generateXMLFromMetrics(
    values.AV,
    values.AC,
    values.PR,
    values.UI,
    values.S,
    values.C,
    values.I,
    values.A,
    values.E,
    values.RL,
    values.RC,
    values.CR,
    values.IR,
    values.AR,
    values.MAV,
    values.MAC,
    values.MPR,
    values.MUI,
    values.MS,
    values.MC,
    values.MI,
    values.MA
  );

  if (!xmlResult.success) {
    return { success: false, error: xmlResult, values, result };
  }

  return {
    success: true,
    values,
    result,
    xmlString: xmlResult.xmlString,
  };
}

async function updateSelectionFromForm(form) {
  const errorMessage = document.getElementById("error-message");

  if (Office.context.host !== Office.HostType.Word) {
    renderError(errorMessage, "Updating the document selection is only available in Word.");
    return;
  }

  const calculation = handleCalculate(form);
  if (!calculation || !calculation.success) {
    return;
  }

  try {
    const vectorString = calculation.result.vectorString;
    await Word.run(async (context) => {
      const selection = context.document.getSelection();
      selection.insertText(vectorString, Word.InsertLocation.replace);
      await context.sync();
    });
  } catch (error) {
    renderError(errorMessage, "Unable to update selection. Try again.");
    console.error("Failed to update selection with CVSS vector:", error);
  }
}

async function insertResultsIntoDocument(form) {
  const errorMessage = document.getElementById("error-message");

  if (Office.context.host !== Office.HostType.Word) {
    renderError(errorMessage, "Inserting results is only available in Word.");
    return;
  }

  const calculation = handleCalculate(form);
  if (!calculation || !calculation.success) {
    return;
  }

  try {
    await Word.run(async (context) => {
      const selection = context.document.getSelection();
      selection.insertText(calculation.result.vectorString, Word.InsertLocation.replace);
      await context.sync();
    });
    renderError(errorMessage, null);
  } catch (error) {
    renderError(errorMessage, "Unable to insert results. Try again.");
    console.error("Failed to insert CVSS results into document:", error);
  }
}
